<template>
  <div id="app">
   <button @click="payClick"></button>
   <payment 
   :payShow="payShow" 
   :payStatus="payStatus" 
   :payTitle = "payTitle"
   @paySubmit="submitFns" 
   @payClose="payShow=false"
   >
    </payment>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
        payShow: false,
        payStatus: false,
        payTitle:""
    }
  },
  methods:{
    payClick:function(){
        // 
        this.payShow = true;
    },
    submitFns:function(value){
        // 
        if(value=='123456'){
            //
            setTimeout(function(_this){
                _this.payStatus = true;
            },100,this)
        }else{
            // 
            setTimeout(function(_this){
                _this.payStatus = false;
            },100,this)
        }
    }
  },
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

button{
    font-size: 16px;
    background: #42b983;
    color: #fff;
    padding: 10px;
    border:none;
    border-radius: 5px;
    outline: none;
}

</style>



